
body {
  background: #F2F2F2;
}
@-webkit-keyframes fangda {
0% {
    opacity: 1;
}
20% {
    top: 0px;
    width: 100%;
    height: 100%;
    left: 0px;
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.8);
}
40% {
    top: 0px;
    width: 105%;
    height: 105%;
    top: -5px;
    left: -5px;
    box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.6);
}
60% {
    top: 0px;
    width: 110%;
    height: 110%;
    top: -10px;
    left: -10px;
    box-shadow: 0 0 0 12px rgba(0, 0, 0, 0.4);
}
80% {
    top: 0px;
    width: 115%;
    height: 115%;
    top: -15px;
    left: -15px;
    box-shadow: 0 0 0 16px rgba(0, 0, 0, 0.2);
}
100% {
    top: 0px;
    width: 120%;
    height: 120%;
    top: -20px;
    left: -20px;
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
}
}
@keyframes fangda {
0% {
    opacity: 1;
}
20% {
    top: 0px;
    width: 100%;
    height: 100%;
    left: 0px;
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.8);
}
40% {
    top: 0px;
    width: 105%;
    height: 105%;
    top: -5px;
    left: -5px;
    box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.6);
}
60% {
    top: 0px;
    width: 110%;
    height: 110%;
    top: -10px;
    left: -10px;
    box-shadow: 0 0 0 12px rgba(0, 0, 0, 0.4);
}
80% {
    top: 0px;
    width: 115%;
    height: 115%;
    top: -15px;
    left: -15px;
    box-shadow: 0 0 0 16px rgba(0, 0, 0, 0.2);
}
100% {
    top: 0px;
    width: 120%;
    height: 120%;
    top: -20px;
    left: -20px;
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
}
}
@-webkit-keyframes xiangs {
0% {
    top: -30px;
}
10% {
    top: -50px;
}
20% {
    top: -70px;
}
30% {
    top: -90px;
}
40% {
    top: -110px;
}
50% {
    top: -130px;
}
60% {
    top: -150px;
}
70% {
    top: -170px;
}
80% {
    top: -190px;
}
90% {
    top: -210px;
}
100% {
    top: -230px;
}
}
@keyframes xiangs {
0% {
    top: -30px;
}
10% {
    top: -50px;
}
20% {
    top: -70px;
}
30% {
    top: -90px;
}
40% {
    top: -110px;
}
50% {
    top: -130px;
}
60% {
    top: -150px;
}
70% {
    top: -170px;
}
80% {
    top: -190px;
}
90% {
    top: -210px;
}
100% {
