}
}
.col-md-12 > h3 {
  display: inline-block;
}
.col-md-12 > h3 > p {
  margin-top: 5px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.8);
}
.icon-stack {
  margin-right: 5px;
}
#project_box {
  padding: 3.2em 0;
  background: #F2F2F2;
}
.project {
  margin: 20px 0;
}
.project > li {
  float: left;
  position: relative;
  margin-right: 40px;
}
.project > li:first-child > a:hover:after {
  /*display: none;*/
  -webkit-animation: fangda 1s 1;
          animation: fangda 1s 1;
}
.project > li:first-child > a {
  position: relative;
}
.project > li:first-child > a:after {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0px #000;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  top: 0;
  left: 0;
}
.project > li:first-child > a img {
  border-radius: 50%;
}
.project > li:nth-child(2) > a:hover > img {
  -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
}
.project > li:last-child > a {
  overflow: hidden;
}
.project > li:last-child > a:hover img {
  -webkit-animation: xiangs .7s 1;
          animation: xiangs .7s 1;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
.project > li > p {
  margin: 20px 0;
  text-align: center;
  font-size: 18px;
}
.project > li > a {
  display: table-cell;
  width: 200px;
  height: 200px;
  vertical-align: middle;
}
.project > li > a > img {
  max-width: 100%;
  max-height: 100%;
  -webkit-transition: all 0.5s linear;
  transition: all 0.5s linear;
  position: relative;
}



