
.modal-img {
  position: relative;
}
.modal-img div {
  display: none;
  width: 200px;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 100;
}
.modal-img div img {
  width: 100%;
}
.contact-top {
  margin-bottom: 20px;
}
#contact {
  padding: 3.2em 0;
  background: #2c3e50;
  color: #fff;
}
#contact h4 {
  margin-bottom: 0.5em;
}
#contact p {
  font-weight: normal;
}
#contact .commenity img {
  border-radius: 50%;
}
#contact .commenity {
  margin-bottom: 20px;
}
#contact .col-md-3 .col-md-12 {
  margin-bottom: 0.3em;
}



