
#about {
  background: #2c3e50;
  color: #eee;
  padding: 3.2em 0;
}
#resume {
  background: #F2F2F2;
}
#resume .row {
  padding: 3.2em 0;
  border-bottom: 1px solid #ccc;
}
#resume .row h4 {
  color: #2c3e50;
  font-weight: 700;
  padding-bottom: 0.5em;
}
#resume .row:last-child {
  border: none;
}



