
@import 'assets/css/icons.css';
@import 'assets/css/bootstrap.css';
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul,
li,
dl,
dt,
h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
.app > div.section {
  margin-top: 59px;
}
.app > div.section .col-md-2 span {
  margin-right: 5px;
}
.app .clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.app-right {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}
.app-right-box {
  background: rgba(0, 0, 0, 0.8);
}
.app-right-box > ul {
  width: 100%;
  padding: 10px 0;
  text-align: center;
}
.app-right-box > ul li {
  display: inline-block;
  padding: 0 10px;
  margin-right: 10px;
  cursor: pointer;
}
.app-right-box > ul li:last-child {
  margin: 0;
}
.app-right-box > ul li > a {
  display: block;
}
.app-right-box > ul li:hover i {
  opacity: .65;
}
.app-right-box > ul li i:before {
  font-size: 35px;
  color: #fff;
}
.app-right-box > ul li span {
  display: none;
}



